.nextPageDiv {
  width: 100%;
  height: 4rem;
  padding: 1rem 0 3rem 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.nextPageDiv button {
  padding: 0.75rem 2rem;
  font-size: 1.2rem;
