$color-primary: #73bf86;
$color-secondary-white: #f2f2f2;
$color-secondary-tan: #f2d3ac;
$color-secondary-green: #a0d9be;
$color-primary-hover: #56bf7b;

.recipeViewBackDrop {
  position: fixed;
  overflow-y: hidden;
  top: 4rem;
  left: 0;
  width: 100%;
  height: calc(100vh - 4rem);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 999;
  background-color: rgba(128, 128, 128, 0.5);
  cursor: default;

  .recipeViewCard {
    position: relative;
    background-color: white;
    border-radius: 0.5rem;
    padding: 3rem;
    width: 75%;
    max-height: 75%;
    display: flex;
    flex-direction: column;
    row-gap: 1rem;
    overflow-y: auto;

    .headerRow {
      display: grid;
      column-gap: 4rem;
      align-items: center;
      grid-template-columns: 1fr 1fr;
      row-gap: 0.5rem;

      .imgDiv {
        grid-column: 2 / 3;
        grid-row: 1 / 6;
        width: 100%;
        display: flex;
        align-self: flex-start;
        align-items: flex-start;
        justify-content: center;
        img {
          border-radius: 1rem;
          max-width: 100%;
          height: auto;
        }
      }

      h2 {
        font-size: 1.7rem;
        padding: 1rem 0 0 0;
        grid-column: 1 / 2;
        grid-row: 1 / 2;
        height: min-content;
      }

      .sourceTag {
        height: min-content;
        cursor: pointer;
        text-decoration: none;
        color: $color-primary;
        grid-row: 2 / 3;

        &:hover {
          color: $color-primary-hover;
          text-decoration: underline;
        }
      }

      .buttonDiv {
        width: 100%;
        display: flex;
        column-gap: 1rem;
        padding: 1rem 0;

        a {
          width: 50%;
          cursor: pointer;

          button {
            width: 100%;
            font-weight: 600;
            padding: 0.5rem;
            border-radius: 0.5rem;
          }
        }

        button {
          width: 50%;
          font-weight: 600;
          padding: 0.5rem;
          border-radius: 0.5rem;
          cursor: pointer;
        }

        .primaryBtn {
          background-color: $color-primary;
          color: $color-secondary-white;
          border: none;

          &:hover {
            background-color: $color-primary-hover;
          }
        }

        .secondaryBtn {
          background-color: $color-secondary-white;
          color: $color-primary;
          border: 1px solid $color-primary;

          &:hover {
            background-color: $color-primary;
            color: $color-secondary-white;
          }
        }
      }

      .notesDiv {
        width: 100%;
        display: flex;
        flex-direction: column;
        row-gap: 0.5rem;

        h3 {
          font-size: 1.3rem;
        }

        .addNoteText {
          cursor: pointer;

          &:hover {
            text-decoration: underline;
          }
        }

        .saveRecipeText {
          cursor: pointer;

          &:hover {
            text-decoration: underline;
          }
        }
      }

      .addNote {
        width: fit-content;
        display: flex;
        flex-direction: column;
        row-gap: 0.5rem;
        position: relative;
        width: 100%;

        h3 {
          font-size: 1.3rem;
        }

        form {
          width: 100%;
          display: flex;
          flex-direction: column;
          row-gap: 0.5rem;

          .textArea {
            width: 100%;
            height: 5rem;
            resize: none;
            background-color: $color-secondary-white;
            border: 1px solid $color-primary;
            border-radius: 0.5rem;
            padding: 0.5rem;
          }

          .buttonDiv {
            display: flex;
            width: 100%;
            column-gap: 1rem;
            button {
              width: 50%;
              cursor: pointer;
              font-size: 1.1rem;
              transition: 0.25s;
            }

            .primaryBtn {
              background-color: $color-primary;
              color: $color-secondary-white;
              border: 1px solid transparent;
              border-radius: 0.25rem;
              padding: 0.25rem 0;

              &:hover {
                background-color: $color-primary-hover;
              }
            }

            .secondaryBtn {
              background-color: $color-secondary-white;
              color: $color-primary;
              border: 1px solid transparent;
              border-radius: 0.25rem;
              padding: 0.25rem 0;

              &:hover {
                border: 1px solid $color-primary-hover;
                color: $color-primary-hover;
              }
            }
          }
        }
        .closeIconDiv {
          position: absolute;
          top: 0;
          right: 0;
          width: 2rem;
          height: 2rem;
          border-radius: 0.25rem;
          display: flex;
          align-items: center;
          justify-content: center;
          transition: 0.2s;

          cursor: pointer;
          .closeIcon {
            width: 1.5rem;
            height: 1.5rem;
            transition: 0.2s;
          }
          &:hover {
            background-color: white;

            .closeIcon {
              color: $color-primary;
            }
          }
        }
      }
    }

    .secondRow {
      display: flex;
      flex-wrap: wrap;
      justify-content: flex-start;
      column-gap: 4rem;
      row-gap: 1.5rem;

      .overviewDiv {
        display: flex;
        flex-direction: column;
        column-gap: 2rem;
        row-gap: 0.5rem;
        width: fit-content;

        h3 {
          font-size: 1.3rem;
        }

        .items {
          display: flex;
          column-gap: 0.5rem;
          align-items: center;
          p {
            border: 1px solid $color-primary;
            padding: 0.5rem;
            border-radius: 0.5rem;
            font-weight: 600;
          }
        }
      }
    }

    .infoDiv {
      display: flex;
      flex-direction: column;
      row-gap: 1rem;

      h3 {
        font-size: 1.3rem;
      }

      .infoItems {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        gap: 0.5rem;

        .ingredientDiv {
          max-width: fit-content;
          border: 1px solid $color-primary;
          border-radius: 0.5rem;
          background-color: $color-secondary-green;
          display: flex;
          align-items: center;
          justify-content: center;
          text-align: center;
          padding: 0.5rem;
        }
      }
    }

    .exitIconDiv {
      position: absolute;
      top: 1rem;
      right: 1rem;
      width: 2.5rem;
      height: 2.5rem;
      border-radius: 0.5rem;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;

      .exitIcon {
        width: 1.5rem;
        height: 1.5rem;
      }

      &:hover {
        background-color: $color-secondary-white;
      }
    }

    &::-webkit-scrollbar {
      background-color: $color-secondary-white;
      border-radius: 0.5rem;
      width: 0.5rem;
    }
    &::-webkit-scrollbar-thumb {
      background-color: $color-primary;
      border-radius: 0.5rem;
      &:hover {
        background-color: $color-primary-hover;
      }
    }

    .saveIconDiv {
      position: absolute;
      top: 1rem;
      left: 1rem;
      width: 2.5rem;
      height: 2.5rem;
      border-radius: 0.5rem;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;

      .deleteRecipeIcon {
        color: $color-primary;
        width: 1.5rem;
        height: 1.5rem;

        &:hover {
          color: $color-primary-hover;
          width: 1.6rem;
          height: 1.6rem;
        }
      }

      .saveRecipeIcon {
        color: $color-primary;
        width: 1.5rem;
        height: 1.5rem;

        &:hover {
          color: $color-primary-hover;
          width: 1.6rem;
          height: 1.6rem;
        }
      }
    }
  }
}

@media screen and (max-width: 950px) {
  .recipeViewBackDrop {
    .recipeViewCard {
      overflow-x: hidden;
      padding: 2rem 1rem;
      align-items: center;
      max-height: 80%;
      width: 90%;
      row-gap: 2rem;

      .headerRow {
        column-gap: 1rem;
        padding-top: 1rem;

        .imgDiv {
          grid-row: 2 / 6;
        }

        h2 {
          text-align: left;
          grid-column: 1 / 3;
        }
        .buttonDiv {
          display: flex;
          flex-direction: column;
          row-gap: 1rem;

          a {
            width: 100%;
          }

          button {
            width: 100%;
          }
        }

        .notesDiv {
          grid-column: 1 / 3;
        }
        .addNote {
          grid-column: 1 / 3;

          form {
            .buttonDiv {
              flex-direction: row;
              button {
                width: 50%;
              }
            }
          }
        }
      }

      .primaryInfoRow {
        flex-direction: column;
        row-gap: 1rem;
        .overviewDiv {
        }
      }

      .exitIconDiv {
        top: 0.5rem;
        right: 0.5rem;
      }

      .saveIconDiv {
        top: 0.5rem;
        left: 0.5rem;
      }
    }
  }
}
